








































































































































import uniNavBar from '../../components/uni-nav-bar.vue';
import uniIcon from '../../components/uni-icon.vue';
import uniLoadMore from '@/components/uni-load-more.vue';
import {
	mapState,
	mapMutations
} from 'vuex';

export default {
	computed: mapState([ 'hasLogin','userInfo']),
	components: {
		uniNavBar,
		uniIcon,
		uniLoadMore,
	},
	
	data() {
		return {
			/* 导航控制 */
			isup1:1,
			isup2:2,
			isup3:3,
			isactive: 0,
			/* 导航控制 */
			/* 导航定位 */
			
		
			isscroll:false,
			
			/* 海报控制 */
			indicatorDots: true,
			autoplay: true,
			interval: 5000,
			duration: 500,
			/* 海报控制 */
			
			/* 加载更多 */
			loadingType: 0,
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			/* 加载更多 */
				
				//滚动消息
				msg : [
					'在淘宝天猫区发布了1条(10份)试用活动',
					'在淘宝天猫区发布了1条任务',
					'完成试用返还担保金和红包奖励,编号:07192**** ',
					'购买[一年皇冠VIP会员卡]充值卡，扣费1000.00元 ',
					'账户余额充值，单号：R07172****',
					'在淘宝天猫区发布了1条(2份)试用活动',
					'在淘宝天猫区发布了1条任务',
				],
			newsitems:[],//商品信息
			tabBars:[
				{
					name: '全部',
					id: '0'
				},
			],
			limit:2,
			request:true,
		}
		
	},
	
	
	onLoad() {
		
		uni.request({
			url:"http://t2.qingxinedu.cn/api/index/index",
			dataType:'text',
			method:"GET",
			success: (res) => {//请求成功后返回的数据
				//console.log(this.userInfo)
				const data = JSON.parse(res.data);
				this.tabBars = this.tabBars.concat(JSON.parse(data.cate));
				this.newsitems = data.data;
			}
		});
	
	},
	
	
	
	
	methods: {
		LoadMore(){
			
		
			if(this.loadingType !==2){
				this.loadingType = 1;
				if(this.request){
					this.request=false;
					setTimeout(() => {
						this.addData();
					}, 1200);
				}
			}	
		},
		addData(){
			
			uni.request({
				url: 'http://t2.qingxinedu.cn/api/index/index',
				method: 'GET',
				data: {limit:this.limit},
				success: (res) => {
					const loadData = res.data;
					if(loadData.data.length==0){
						this.loadingType=2;			
					}else{
						this.newsitems =this.newsitems.concat(loadData.data) 
						this.limit = this.limit+1;
						this.request = true;
					}
					
				    }
			    });
				
		},
		//导航控制
		tapTab(index){
			uni.navigateTo({
				url:"../info/info?cid="+index,
			})
				
		},
		/* 滑动定位及加载更多 */
		
	
		/* 滑动定位及加载更多 */
		
		//子分类控制
		activefn(n,isup=1) {
			this.isactive = n
			if(isup==1||isup==-1){
				this.isup1=-isup
				this.sort(this.isup1)
			}else if(isup==2||isup==-2){
				this.isup2=-isup
				this.sort(this.isup2)
			}else if(isup==3||isup==-3){
				this.isup3=-isup
				this.sort(this.isup3)
			}else if(n==0){
				this.sort(0)
			}
		},
		sort(sort_id){
			
			uni.request({
				url:"http://t2.qingxinedu.cn/api/index/index?"+'order='+sort_id,
				method:"GET",
				success: res=>{
					this.newsitems = res.data.data;
				}
			})
		},
		
		
		scrollyfn(e){		
			this.isscroll=e.detail.scrollTop>10
			//console.log(e.detail.scrollTop)
		},
		
	}
}
